import { Pipe, PipeTransform, transition } from '@angular/core';

@Pipe({
    name: "transAsset"
})

export class TransAssetPipe implements PipeTransform {

    transform(code: string) {
        const translation = {
            FIXED_INCOME: '',
            ALTERNATIVE: '',
            CASH: '',
            STOCK: '',
        };

        return translation[code] || code;
    }

}


