import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: "transTradeType"
})

export class TransTradeTypePipe implements PipeTransform {

    transform(code: string) {
        const translation = {
            NEW: '',
            APPEND: '',
            REBALANCE: '',
            REDEEM: '',
            REDEEM_ALL: ''
        };

        return translation[code] || code;
    }
}


